<template>
  <div class="hs-container" v-if="!this.noValue(JSON.stringify(details))">
    <c-header :title="$t('base.OrderDetails')" isBackNav></c-header>
    <hs-scroll
      class="table-view"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_getOrderDetails">
      <ul>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="state-details">
              <div class="header" :type="language">
                <p>{{details.OrderStatusDesc}}</p>
                <p>{{details.VettingStatusDesc}}</p>
                <p>{{details.OrderStatusDesc}}</p>
              </div>
              <p class="infor">{{$t('base.OrderNumber')}}: {{details.OrderNumber}}</p>
              <p class="infor">{{$t('base.OrderDate')}}: {{$t(beTime(details.BookDate), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}&nbsp;{{beTime(details.BookDate, 'hh:mm')}}</p>
              <div class="buttons" v-if="$route.query.isOwner">
                <!-- <hs-button
                  class="button"
                  @click="_gotoPay"
                  v-if="details.SettlementType === $t('base.SettleNow') && Number(details.PayStatus) === 0 && (details.OrderStatus === 10 || details.OrderStatus === 20 || details.OrderStatus === 30) && (Number(details.VettingStatus) === 0 || Number(details.VettingStatus) === 5)">
                  {{$t('base.Pay')}}
                </hs-button>
                <hs-button class="button" type="common" @click="_cancel" v-if="details.OrderStatus === 10">{{$t('base.CancelOrder')}}</hs-button> -->
                <hs-button v-if="details.PageBtnInfo.IsAllowPay" @click="_gotoPay"
                  class="button">{{$t('base.Pay')}}</hs-button>
                <hs-button v-if="details.PageBtnInfo.IsAllowCancel" @click="_cancel"
                  class="button" type="common">{{$t('base.CancelOrder')}}</hs-button>
              </div>
              <div class="buttons" v-if="infoIndex >= 0 && $route.query.isExamine && Number(details.VettingStatus) === 1">
                <hs-button class="button" @click="[vettingContent = 1, showAlert = true]">{{$t('base.Pass')}}</hs-button>
                <hs-button class="button" type="common" @click="[vettingContent = 0, showAlert = true]">{{$t('base.Reject')}}</hs-button>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-hotel">
              <p>{{details.HotelInfo.HotelName}}</p>
              <p>{{details.RoomAmount}}{{$t('base.QuantifierHotel')}} {{details.HotelInfo.RoomType}}</p>
              <p>{{details.HotelInfo.BedType}}<template v-if="details.HotelInfo.BedType && details.HotelInfo.BreadFast">&nbsp;|&nbsp;</template>{{details.HotelInfo.BreadFast}}</p>
              <p>{{$t('base.CheckInDate')}} {{$t(beTime(details.CheckInDate), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}</p>
              <p>{{$t('base.CheckOutDate')}} {{$t(beTime(details.CheckOutDate), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}} {{$t('base.TotalNights', 'function', getApartDayCount(this.beTime(details.CheckInDate), this.beTime(details.CheckOutDate)))}}</p>
              <p v-if="details.ArrivalTime">{{$t('base.GoToTheStoreBeforeOClock', 'function', details.ArrivalTime)}}</p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-cancel">
              <div style="background-image: url('static/image/hotel/irreversible.png')"></div>
              <div>
                <p>{{details.CancelRuleDesc}}</p>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="custom-cell-title">
              <div class="right">
                <div class="address">
                  <div class="logo" style="background-image: url('static/image/hotel/location.png')"></div>
                  <p>{{details.HotelInfo.CityName}}{{details.HotelInfo.DistrictName}}{{details.HotelInfo.HotelAddress}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 logoName="call.png" v-if="details.HotelInfo.Telephone" @click.native="_callPhone(details.HotelInfo.Telephone)">
            <div class="custom-cell-title">
              <div class="right">
                <p>{{$t('base.ContactHotel')}}：{{details.HotelInfo.Telephone}}</p>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in details.Guests" :key="index">
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.Roomer')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === details.Guests.length - 1 ? '0px' : '1px'}">
                <div class="name">{{item.Name}}</div>
                <div class="others">
                  <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-small-title v-if="details.SpecificRequirements"
            :title="$t('base.SpecificRequirements')"
            type="bold"
            :value="details.SpecificRequirements"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.CustomItem && details.DisplayConfig"
            :title="details.DisplayConfig.CustomItemName"
            type="bold"
            :value="details.CustomItem"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.Purpose"
            :title="$t('base.Purpose')"
            type="bold"
            :value="details.Purpose"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title
            :title="$t('base.BookingSource')"
            type="bold"
            :value="details.BookingSourceDesc"
            :showLogo="false">
          </hs-cell-small-title>
          <template v-if="language === 'CN'">
            <hs-cell-small-title v-if="details.VettingRecordInfos" @click.native="showExamineProcess = true"
              :title="$t('base.ApprovalInformation')"
              type="bold">
            </hs-cell-small-title>
          </template>
          <template v-else>
            <hs-cell-small-title v-if="details.VettingRecordInfos" @click.native="showExamineProcess = true"
              :title="$t('base.ApprovalInformation')"
              type="bold"
              :value="details.VettingStatusDesc">
            </hs-cell-small-title>
          </template>
          <hs-cell-small-title v-if="Number(details.TravelType) === 0"
            :title="$t('base.PaymentWay')"
            type="bold"
            :value="details.PayType | filterPayType"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="details.ReasonCode"
            :title="$t('base.ViolationOfRank')"
            type="bold"
            :value="details.ReasonCode"
            :warning="false"
            :showLogo="false"
            @header="_getTravelStandard">
          </hs-cell-small-title>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in details.Contacts" :key="index">
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.ContactInformation')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === details.Contacts.length - 1 ? '0px' : '1px'}">
                <div class="name">{{item.Name}}</div>
                <div class="others">
                  <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                  <p>{{$t('base.Email')}} {{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>
    <div class="footer" @click="_switchDetailsDialog">
      <div class="price">
        <p>{{$t('base.TotalAmount')}}</p>
        <p>￥{{details.TotalPrice}}</p>
      </div>
      <div class="details">
        <div class="logo" :style="{transform: showDetailsDialog ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
    </div>
    <hs-dialog v-model="showDetailsDialog" height="calc(100% - 50px)" ref="detailsDialog">
      <hs-scroll class="detailsDialog" :data="[details]" @click.native.stop>
        <ul>
          <hs-cell-1 :showLogo="false">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.PriceHotel')}}</p>
              <p><span>￥{{details.TotalRoomPrice}}</span><span></span></p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="detailsDialog-custom-cell">
              <p>{{$t('base.ServiceFee')}}</p>
              <p><span>￥{{details.ServicePrice}}</span><span></span></p>
            </div>
          </hs-cell-1>
        </ul>
      </hs-scroll>
    </hs-dialog>
    <c-examine-process v-model="showExamineProcess" :businessName="$t('base.Roomer')" :datalist="details.VettingRecordInfos" :passengers="details.Guests"></c-examine-process>
    <infor-details-map v-model="showMap" :details="details.HotelInfo"></infor-details-map>
    <hs-alert :show.sync="showAlert" :title="vettingContent === 1 ? $t('base.ApprovedPass') : $t('base.ApprovalRejection')" cancelBtn="" @confirm="_examineSubmit" maskClose>
      <div class="hs-alert-content" slot="content">
        <input maxlength='50' v-model="vettingMemo" :placeholder="$t('base.PleaseEnterAComment')"/>
      </div>
    </hs-alert>
    <c-travel-standard v-model="showTravelStandard" :type="2" :details="hotelRight.HotelRank" :travelPolicy="hotelRight.TravelPolicy"></c-travel-standard>
    <pay ref="Pay"></pay>
  </div>
</template>
<script type="text/ecmascript-6">
// 酒店订单详情
import Vue from 'vue'
import CHeader from 'components/c-header/c-header';
import HsDialog from 'base/hs-dialog/hs-dialog';
import HsScroll from 'base/hs-scroll/hs-scroll';
import HsSection from 'base/hs-tableview/hs-section/hs-section';
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1';
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsButton from 'base/hs-button/hs-button';
import eventBus from 'common/js/eventBus.js';
import InforDetailsMap from '../../infor-details/infor-details-map'
import CExamineProcess from 'components/c-examine-process/c-examine-process';
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import HsAlert from 'base/hs-alert/hs-alert'
import Pay from 'pages/pay/pay'
import { mapGetters, mapActions } from 'vuex'
import {
  kpiHotelgetOrderDetail,
  kpiHotelCancelOrder,
  kpiVettingVettingHandleOrder
} from 'api';
export default {
  created() {
    if (this.noValue(this.$route.query.OrderID)) {
      this.$router.replace('/hotel/orderList');
    } else if (!this.noValue(this.$route.query.OrderID)) {
      this._getOrderDetails(); // 获取订单详情
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.showExamineProcess || this.showMap) {
      this.showExamineProcess = false
      this.showMap = false
      next(false)
    } else {
      this.showAlert = false
      next()
    }
  },
  computed: {
    ...mapGetters([
      'hotelRight'
    ]),
    infoIndex() {
      return this.details.VettingRecordInfos ? this.details.VettingRecordInfos.map(v => v.TemplateNodeProcessStatusDesc).indexOf('进行中') : -1
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {},
      showDetailsDialog: false,
      showExamineProcess: false, // 审批流
      vettingContent: 1, // 0：否决 1：通过
      vettingMemo: '',
      showMap: false,
      showAlert: false,
      showTravelStandard: false
    }
  },
  methods: {
    ...mapActions([
      'dispatchHotelRight'
    ]),
    // 获取酒店订单详情接口
    _getOrderDetails() {
      kpiHotelgetOrderDetail({
        orderID: this.$route.query.OrderID
      }).then(res => {
        console.log('订单详情: ', res.ResultData);
        this.details = res.ResultData;
      });
    },
    _switchDetailsDialog() {
      if (this.showDetailsDialog) {
        this.$refs.detailsDialog.showDetails = false;
      } else {
        this.showDetailsDialog = true;
      }
    },
    // 打电话
    _callPhone(mobile) {
      window.location.href = `tel:${mobile}`
    },
    /** 支付 */
    _gotoPay(item) {
      this.$refs.Pay && this.$refs.Pay.gotoPay({
        OrderBusinessType: 2,
        OrderID: this.$route.query.OrderID,
        PayWay: 3,
        TravelType: Number(this.details.TravelType),
        CallbackPathName: 'hotelOrderList'
      })
    },
    // 酒店订单取消
    _cancel() {
      this.$alert({
        content: this.$t('TM.AreYouSureToCancelTheOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            orderID: this.$route.query.OrderID
          }
          kpiHotelCancelOrder(params).then(res => {
            if (res.ResultData.IsSuccess === true) {
              this.$toast(this.$t('TM.OrderCancelled'));
              setTimeout(() => { eventBus.$emit(`refresh`) }, 0);
              this.$router.replace({ name: 'hotelOrderList', params: { TravelType: Number(this.details.TravelType) } })
            } else {
              this.$toast(this.$t('TM.OrderCancelFailed'))
            }
          })
        }
      })
    },
    // 审批
    _examineSubmit() {
      let params = {
        ID: this.$route.query.OrderID,
        InstanceID: this.details.VettingRecordInfos[this.infoIndex].InstanceID,
        TemplateNodeID: this.details.VettingRecordInfos[this.infoIndex].TemplateNodeID,
        VettingContent: this.vettingContent,
        VettingMemo: this.vettingMemo,
        BusinessType: 4 // 国内酒店，暂时是写死的
      };
      kpiVettingVettingHandleOrder(params).then(res => {
        console.log('审批: ', res);
        if (res.ResultData === true) {
          this.$toast(this.$t('TM.ApprovalSubmitted'), {
            complete: () => {
              setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
              this.$router.replace({path: '/message/examine', query: {QuerySource: 0}})
            }
          });
        }
      });
    },
    // ================================================================================================
    // 获取差旅职级
    _getTravelStandard() {
      this.dispatchHotelRight().then((res) => {
        this.showTravelStandard = true
      })
    }
  },
  components: {
    CHeader,
    HsDialog,
    HsScroll,
    HsSection,
    HsCell1,
    HsCellSmallTitle,
    HsButton,
    CExamineProcess,
    InforDetailsMap,
    HsAlert,
    CTravelStandard,
    Pay
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 45px - 50px)
    overflow hidden
    .state-details
      width 100%
      padding 15px 0
      background-color $color_white
      >.header
        @extend .flex-row
        @extend .flex-between
        width 100%
        margin-bottom 8px
        >p
          &:last-child
            visibility hidden
      >.header[type="EN"]
        font-size 0.8rem
        >p
          &:nth-child(3)
            display none
      >.infor
        width 100%
        font-size 0.8rem
        color $color_gray_text
        line-height 1.4em
      >.buttons
        @extend .flex-row
        width 100%
        >.button
          flex 1
          margin 10px 5px 0 5px
          &:first-child
            margin-left 0px
          &:last-child
            margin-right 0px
    .custom-cell-hotel
      @extend .flex-column
      width 100%
      padding 15px 0
      >p
        line-height 1.5em
        &:nth-child(1)
          font-size 1.1rem
          font-weight bold
        &:nth-child(2)
          font-size 0.8rem
          font-weight bold
        &:nth-child(3)
        &:nth-child(4)
        &:nth-child(5)
          font-size 0.8rem
          color $color_gray_text
    .custom-cell-cancel
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 15px 0
      >div
        &:nth-child(1)
          width 30px
          height 30px
          background-size 100% 100%
          margin-right 10px
        &:nth-child(2)
          width calc(100% - 40px)
          >p
            line-height 1.3em
            &:nth-child(1)
              font-size 0.9rem
              color $color_theme
            &:nth-child(2)
              font-size 0.8rem
              color $color_gray_text
    .custom-cell-title
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 15px 0
      >.right
        width calc(100% - 60px)
        >p
          font-size 0.9rem
          color $color_black_text_0
          line-height 1.5em
        >.name
          font-size 1.2rem
          font-weight bold
          line-height 1.2em
        >.logos
          @extend .flex-row
          @extend .flex-start
          flex-wrap wrap
          width 100%
          margin 3px 0
          >.logo
            width 15px
            height 15px
            background-size 100% 100%
            margin 5px 10px 5px 0
        >.address
          @extend .flex-row
          @extend .flex-start
          @extend .flex-top
          width 100%
          >.logo
            width 16px
            height 16px
            background-size 100% 100%
            margin-right 4px
            margin-top 2px
          >p
            width calc(100% - 20px)
            font-size 0.8rem
            color $color_black_text_0
            line-height 1.5em
      >.left
        width 60px
        font-size 0.8rem
        color $color_theme
        text-align right
    .custom-cell-people
      @extend .flex-between
      @extend .flex-start
      @extend .flex-top
      width 100%
      >.left
        width 90px
        padding 15px 0
        >p
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          color $color_black_text_1
          line-height 1.4em
      >.right
        width calc(100% - 100px)
        padding 15px 0
        border-bottom 1px dashed $color_gray_layer
        >.name
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          font-weight bold
          line-height 1.4em
        >.others
          width 100%
          >p
            line-height 1.5em
            color $color_gray_text
            font-size 1rem
            &:first-child
              margin-top 7px
    .cell-3-value
      font-weight bold
    .cell-3-title
      @extend .flex-row
      @extend .flex-start
      width 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-left 3px
  >.footer
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
    >.price
      @extend .flex-row
      @extend .flex-start
      width calc(100% - 30px)
      height 100%
      >p
        font-size 1rem
        &:nth-child(2)
          color $color_theme
          margin-left 5px
    >.details
      @extend .flex-row
      @extend .flex-between
      width 20px
      height 100%
      >p
        font-size 0.9rem
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        transition all 300ms
  .detailsDialog
    position absolute !important
    width 100%
    left 0
    bottom 0
    background-color $color_white
    max-height 70%
    overflow hidden
    .detailsDialog-custom-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      height 44px
      >p
        font-size 0.8rem
        &:nth-child(1)
          color $color_black_text_0
        &:nth-child(2)
          color $color_theme
          >span
            &:nth-child(2)
              color $color_black_text_0
              margin-left 5px
  .hs-alert-content
    width 100%
    padding 0 20px
    >input
      width 100%
      height 44px
      border 1px dashed $color_gray_layer
      border-radius 2px
      font-size 0.9rem
      text-align center
</style>



